import {Injectable} from '@angular/core';
import {CookieService} from 'ngx-cookie-service';

@Injectable({providedIn: 'root'})
export class CookiesUtilsService {

  constructor(private cookieService: CookieService) {
  }

  guardarInformacionCookie(
    identificadorCookie: string,
    valorDatos: any,
    expiracionTiempo = 5
  ): void {
    const jsonDatos = JSON.stringify(valorDatos);
    this.cookieService.set(
      identificadorCookie,
      jsonDatos,
      expiracionTiempo,
      '/'
    );
  }

  obtenerInformacionCookie(identificadorCookie: string): any {
    const stringCookie = this.cookieService.get(identificadorCookie);
    const jsonCookie = stringCookie ? JSON.parse(stringCookie) : '';
    return jsonCookie;
  }

  eliminarCookies(parametro?: string): void {
    parametro
      ? this.cookieService.delete(parametro)
      : this.cookieService.deleteAll();
  }
}
