import {Pipe, PipeTransform} from '@angular/core';

@Pipe({name: 'humanize'})
export class HumanizePipe implements PipeTransform {
  transform(value: string): string {
    try {
      return value ? value.replace(/_/g, ' ').toLowerCase().replace(/^\w/, c => c.toUpperCase()) : null;
    } catch (e) {
      return value;
    }
  }
}
