import { Component, OnInit } from '@angular/core';
import { AUTENTICAR_USUARIO_SUCCESS } from '@usuario/state-management/actions/usuario.action';
import { Store } from '@ngrx/store';
import { UserIdleService } from '@app/shared/idle-user/idle-usuario.service';
import { ApiService } from '@core/servicios/api.service';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.scss'],
})
export class AppComponent implements OnInit {
  constructor(
    private apiService: ApiService,
    private userIdle: UserIdleService,
    private store: Store<any>
  ) {
    console.log(
      'WebAppOperaciones Version: ',
      require('../../package.json').version + ''
    );
  }

  ngOnInit(): void {
    this.validarSesionUsuario();
  }

  private validarSesionUsuario() {
    this.apiService
      .getCurrentUser()
      .then((cognitoUserSession) => {
        return {
          sub: cognitoUserSession.attributes.sub,
          username: cognitoUserSession.username,
          email: cognitoUserSession.attributes.email,
          rol: cognitoUserSession.attributes['custom:role'],
          uuid: cognitoUserSession.attributes['custom:uuid'],
          pais: cognitoUserSession.attributes['custom:pais'],
        };
      })
      .then(
        (userData) => {
          this.userIdle.startWatching();
          this.store.dispatch({
            type: AUTENTICAR_USUARIO_SUCCESS,
            payload: userData,
          });
        },
        (err) => console.error('ERROR obteniendo session', err)
      );
  }
}
