import { Component, Inject, OnInit } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import { ICustomModal } from '@app/modulos/dashboard/modelo/interfaces/ICustomModal.type';
import { CustomModalType } from '@app/modulos/dashboard/modelo/enums/CustomModalTypes.enum';

@Component({
  selector: 'app-custom-modal',
  templateUrl: './custom-modal.component.html',
  styleUrls: ['./custom-modal.component.scss'],
})
export class CustomModalComponent implements OnInit {
  customModalType = CustomModalType;
  dataModal: ICustomModal;

  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    public dialogRef: MatDialogRef<CustomModalComponent>
  ) {
    this.dataModal = data;
  }
  ngOnInit() {}

  close() {
    this.dialogRef.close();
  }

  esArreglo(value) {
    return value instanceof Array;
  }
}
