import { Injectable } from '@angular/core';
import { CanLoad, Route, Router, UrlSegment } from '@angular/router';
import { Observable } from 'rxjs';
import { environment } from '@env/environment';

@Injectable({
  providedIn: 'root',
})
export class PaisAmbienteGuard implements CanLoad {
  constructor(public router: Router) {}

  canLoad(route: Route, segments: UrlSegment[]): Observable<boolean> | boolean {
    const paisAmbiente = environment.envCountry;
    const paisesValidos = route.data.paises as Array<string>;
    if (paisesValidos.some((pais) => pais === paisAmbiente)) {
      return true;
    } else {
      this.router.navigateByUrl('pais-invalido');
      return false;
    }
  }
}
