import { Injectable } from '@angular/core';
import { MatDialog, MatDialogRef } from '@angular/material/dialog';
import { ICustomModal } from '@app/modulos/dashboard/modelo/interfaces/ICustomModal.type';
import { CustomModalType } from '@app/modulos/dashboard/modelo/enums/CustomModalTypes.enum';
import { CustomModalComponent } from '../componentes/custom-modal/custom-modal.component';

@Injectable()
export class CustomModalService {
  constructor(private dialog: MatDialog) {}

  modalSuccess: ICustomModal = {
    tipo: CustomModalType.SUCCESS,
    panelClass: ['medium-screen-dialog', 'modal-color-aqua'],
    cssClass: 'custom-modal-success',
  };

  modalError: ICustomModal = {
    tipo: CustomModalType.ERROR,
    panelClass: ['medium-screen-dialog'],
    cssClass: 'custom-modal-error',
  };

  modalInfo: ICustomModal = {
    tipo: CustomModalType.INFO,
    width: '100%',
    height: '100%',
    panelClass: ['full-screen-dialog', 'modal-color-aqua'],
    cssClass: 'custom-modal-information',
  };

  modalWarning: ICustomModal = {
    tipo: CustomModalType.WARNING,
    panelClass: ['medium-screen-dialog'],
    cssClass: 'custom-modal-warning',
  };

  modalConfirmation: ICustomModal = {
    tipo: CustomModalType.WARNING,
    panelClass: ['medium-screen-dialog'],
    cssClass: 'custom-modal-warning',
  };

  abrirModal(
    type: CustomModalType,
    data?: any,
    afterClosedModal?: Function
  ): MatDialogRef<CustomModalComponent> | any {
    const config = this.obtenerModal(type);
    let dialog: MatDialogRef<CustomModalComponent, any>;

    dialog = this.dialog.open(CustomModalComponent, {
      height: config.height,
      width: config.width,
      panelClass: config.panelClass,
      data: Object.assign({}, config, { tipo: type }, data),
    });
    dialog.afterClosed().subscribe(() => {
      if (afterClosedModal) {
        afterClosedModal();
      }
    });
    return dialog;
  }

  private obtenerModal(type: CustomModalType) {
    switch (type) {
      case CustomModalType.SUCCESS:
        return this.modalSuccess;
      case CustomModalType.ERROR:
        return this.modalError;
      case CustomModalType.INFO:
        return this.modalInfo;
      case CustomModalType.WARNING:
        return this.modalWarning;
      case CustomModalType.CONFIRMATION:
        return this.modalConfirmation;
    }
  }
}
