import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { LoginComponent } from './vistas/login/login.component';
import { LoginGuardService } from '@core/guards/login.guard';
import { PaisInvalidoComponent } from '@app/modulos/usuario/vistas/pais-invalido/pais-invalido.component';
import { AuthRouterGuard } from '@core/guards/auth-router.guard';

const routes: Routes = [
  {
    path: 'login',
    component: LoginComponent,
    canActivate: [LoginGuardService],
  },
  {
    path: 'pais-invalido',
    component: PaisInvalidoComponent,
    canActivate: [AuthRouterGuard],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class UsuarioRoutingModule {}
