import { Injectable } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivate,
  Router,
  RouterStateSnapshot,
} from '@angular/router';
import { map, take } from 'rxjs/operators';
import { Store } from '@ngrx/store';
import { ApiService } from '@core/servicios/api.service';

@Injectable({
  providedIn: 'root',
})
export class LoginGuardService implements CanActivate {
  constructor(
    private store: Store<any>,
    public router: Router,
    private apiService: ApiService
  ) {}

  async canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Promise<boolean> {
    return this.validarAccesoSegunRol();
  }

  private async validarAccesoSegunRol(): Promise<boolean> {
    try {
      const sesion = await this.apiService.getCurrentUser();
      if (sesion) {
        return this.store
          .select('usuario', 'rol')
          .pipe(
            take(1),
            map((x) => {
              switch (x) {
                case 'operador-back':
                  this.redirect('dashboard');
                  break;
                case 'coach':
                  this.redirect('coaching');
                  break;
                case 'comercial':
                  this.redirect('comercial');
                  break;
                default:
                  this.redirect('rol-invalido');
                  break;
              }
              return false;
            })
          )
          .toPromise();
      } else {
        return true;
      }
    } catch (e) {
      return true;
    }
  }

  redirect(path: string) {
    const currentURL = window.location.href;
    if (currentURL.indexOf(path) === -1) {
      this.router.navigate([path]);
    }
  }
}
