import { Component, Input, OnInit } from '@angular/core';
import { AbstractControl, AbstractControlDirective } from '@angular/forms';

@Component({
  selector: 'app-mostrar-error-formularios',
  template: ` <ul
    *ngIf="
      control.invalid && control?.errors && (control?.dirty || control.touched)
    "
  >
    <li
      *ngFor="let error of listaErrores()"
      class="contextual ng-star-inserted"
    >
      {{ error }}
    </li>
  </ul>`,
  styleUrls: ['./mostrar-error-formularios.component.scss'],
})
export class MostrarErrorFormulariosComponent implements OnInit {
  constructor() {}

  private static readonly errorMessages = {
    required: () => 'Este campo es requerido',
    email: () => 'El correo electrónico no posee un formato válido',
    repetido: () => 'No es posible repetir información',
    minlength: (params) =>
      'El número mínimo de caracteres es ' + params.requiredLength,
    min: (params) => 'El valor minimo es ' + params.min,
    maxlength: (params) =>
      'El número máximo de caracteres es ' + params.requiredLength,
    max: (params) => 'El valor maximo es ' + params.max,
    pattern: (params, contexto, mensaje) => {
      return !contexto
        ? 'Ups, el formato no es válido'
        : MostrarErrorFormulariosComponent.obtenerMensajeSegnContexto(contexto);
    },
    validateEqual: (params, contexto) =>
      'Parece que ' + contexto + '  no coincide. Inténtalo de nuevo.',
    numeroInvalido: (params) => 'Revisa de nuevo, no es un número valido.',
    invalidRegistroPatronal: (params) =>
      'El registro patronal no posee un formato válido.',
    invalidNIT: (params) =>
      'El NIT no posee un formato válido (Ej. XXXXXXXXXXX-X).',
    matDatepickerMin: (params) => 'Fecha invalida',
  };

  @Input() control: AbstractControlDirective | AbstractControl;
  @Input() contexto: string;
  @Input() tipoMensaje = 'pattern';

  static obtenerMensajeSegnContexto(contexto: string): string {
    let mensaje = '';
    switch (contexto) {
      case 'numero':
        mensaje =
          'Debe contener una combinación de al menos un número, una letras mayúsculas y minúsculas ';
        break;

      default:
        mensaje = 'El correo electrónico no posee un formato válido';
    }
    return mensaje;
  }

  ngOnInit() {}

  listaErrores(): string[] {
    let keysErrores = Object.keys(this.control.errors);
    if (keysErrores.some((field) => field === 'required')) {
      keysErrores = keysErrores.filter((field) => field === 'required');
    }
    return keysErrores.map((field) =>
      this.obtenerMensaje(
        field,
        this.control.errors[field],
        this.contexto,
        this.tipoMensaje
      )
    );
  }

  private obtenerMensaje(
    type: string,
    params: any,
    contexto: string,
    mensajePersonalizado: string
  ) {
    return MostrarErrorFormulariosComponent.errorMessages[type](
      params,
      contexto,
      mensajePersonalizado
    );
  }
}
