import { Component, OnInit } from '@angular/core';
import { MatDialog } from '@angular/material/dialog';
import { PageEvent } from '@angular/material/paginator';
import { Sort } from '@angular/material/sort';
import { CustomModalService } from '@app/shared/servicios/custom-modal.service';
import { CustomModalType } from '@app/modulos/dashboard/modelo/enums/CustomModalTypes.enum';
import {
  IInvitacionEmpresaSinContrato,
  IinvitacionesEmpresaSinContratoPaginadas,
} from '@app/modulos/dashboard/modelo/interfaces/IInvitacionEmpresaSinContrato.type';
import { FormControl, Validators } from '@angular/forms';
import { environment } from '@env/environment';
import { ModalActualizarFechaInvitacionEmpresaComponent } from '../modal-actualizar-fecha-invitacion-empresa/modal-actualizar-fecha-invitacion-empresa.component';
import { SharedService } from '@app/shared/servicios/shared.service';

@Component({
  selector: 'app-invitaciones-empresa',
  templateUrl: './invitaciones-empresa.component.html',
  styleUrls: ['./invitaciones-empresa.component.scss'],
})
export class InvitacionesEmpresaComponent implements OnInit {
  invitacionesEmpresa: IInvitacionEmpresaSinContrato[];
  invitacionesEmpresaFiltradas: IInvitacionEmpresaSinContrato[];
  actualizanCompleta = false;
  mensajeCopiado = false;
  _length = 100;
  _pageSize = 10;
  _pageSizeOptions = [10, 25, 50, 100];
  _pageIndex = 0;
  _columnaSeleccionada = 'default';
  _direction = 'default';
  filtroControl: FormControl = new FormControl('', [
    Validators.maxLength(301),
    Validators.minLength(2),
    Validators.pattern(
      '^[a-zA-Z0-9\u00C0-\u00D6\u00D8-\u00F6\u00F8-\u0178][a-zA-Z0-9\u00C0-\u00D6\u00D8-\u00F6\u00F8-\u0178 ().&@,#-]{0,301}$'
    ),
  ]);

  constructor(
    private customModalService: CustomModalService,
    private dialog: MatDialog,
    private sharedService: SharedService
  ) {}

  ngOnInit() {
    this.consultarInvitaciones();
  }

  changePage($event: PageEvent) {
    this._pageIndex = $event.pageIndex;
    this._pageSize = $event.pageSize;
    this.filtroControl.reset();
    this.consultarInvitaciones();
  }

  sortData($event: Sort) {
    if ($event.direction) {
      this._columnaSeleccionada = $event.active;
      this._direction = $event.direction;
    } else {
      this._columnaSeleccionada = 'default';
      this._direction = 'default';
    }
    this.filtroControl.reset();
    this.consultarInvitaciones();
  }

  abrirModalActualizarFechaExpiracion(
    uuidInvitacion: string,
    nombreEmpresa: string
  ) {
    this.dialog
      .open(ModalActualizarFechaInvitacionEmpresaComponent, {
        data: { uuidInvitacion, nombreEmpresa },
        panelClass: 'medium-screen-dialog',
      })
      .afterClosed()
      .subscribe((nuevoComentario) => {
        if (nuevoComentario) {
          this.actualizanCompleta = true;
          setTimeout(() => {
            this.actualizanCompleta = false;
          }, 1500);
        }
      });
  }

  consultarInvitaciones() {
    const offset = this._pageIndex * this._pageSize;
    const valorAFiltrar =
      this.filtroControl.value && this.filtroControl.valid
        ? this.filtroControl.value
        : '';

    this.sharedService
      .consultarInvitacionesEmpresa(
        valorAFiltrar,
        offset,
        this._pageSize,
        this._columnaSeleccionada,
        this._direction
      )
      .subscribe(
        (invEmpresaSinContrato: IinvitacionesEmpresaSinContratoPaginadas) => {
          this.invitacionesEmpresa =
            invEmpresaSinContrato.invitacionesEmpresasSinContrato;
          this.invitacionesEmpresaFiltradas = this.invitacionesEmpresa;
          this._length = invEmpresaSinContrato.totalInvitaciones;
        },
        (err) => this.customModalService.abrirModal(CustomModalType.ERROR)
      );
  }
  copiarLink(campania) {
    const origen = 'M';
    let selBox = document.createElement('textarea');
    selBox.style.position = 'fixed';
    selBox.style.left = '0';
    selBox.style.top = '0';
    selBox.style.opacity = '0';
    const enlace =
      environment.sitioDescargas + '?o=' + origen + '&c=' + campania;
    selBox.value = enlace;
    document.body.appendChild(selBox);
    selBox.focus();
    selBox.select();
    document.execCommand('copy');
    document.body.removeChild(selBox);
    this.mensajeCopiado = true;
    window.open(enlace, 'scrollbars=yes');
    setTimeout(() => {
      this.mensajeCopiado = false;
    }, 2000);
  }

  onKey(e) {
    !this.filtroControl.value && this.consultarInvitaciones();
  }
}
