import { FormControl, Validators } from '@angular/forms';
import { Component, Inject } from '@angular/core';
import { MAT_DIALOG_DATA, MatDialogRef } from '@angular/material/dialog';
import * as momentTimezone from 'moment-timezone';
import { Moment } from 'moment';
import { SharedService } from '@app/shared/servicios/shared.service';

@Component({
  selector: 'app-modal-actualizar-fecha-invitacion-empresa',
  templateUrl: './modal-actualizar-fecha-invitacion-empresa.component.html',
  styleUrls: ['./modal-actualizar-fecha-invitacion-empresa.component.scss'],
})
export class ModalActualizarFechaInvitacionEmpresaComponent {
  error = '';
  uuidInvitacion: string;
  fechaControl: FormControl = new FormControl({ value: '', disabled: true }, [
    Validators.required,
  ]);
  constructor(
    public dialogRef: MatDialogRef<
      ModalActualizarFechaInvitacionEmpresaComponent
    >,
    @Inject(MAT_DIALOG_DATA)
    public data: { uuidInvitacion: string; nombreEmpresa: string },
    private sharedService: SharedService
  ) {
    this.uuidInvitacion = data.uuidInvitacion;
  }

  actualizarFecha(fecha: Moment): void {
    const fechaExpiracion = this.formatearFecha(fecha);
    this.sharedService
      .actualizarFechaExpiracionInvitacionEmpresaSinContrato({
        uuidInvitacion: this.uuidInvitacion,
        fechaExpiracion,
      })
      .then(() => this.dialogRef.close('Exitoso'))
      .catch(() => {
        this.error = ':(, Prueba nuevamente con una fecha diferente.';
      });
  }

  formatearFecha(fecha: Moment): string {
    return momentTimezone(fecha.toDate())
      .tz('America/Mexico_City')
      .hour(23)
      .format('YYYY-MM-DDTHH:mm:ss.SSSZ');
  }
  myFilter = (d: Moment): boolean => {
    const day = d.isAfter(this.obtenerFecha(new Date()));
    return day;
  };

  obtenerFecha(fecha: Date) {
    const hoy = momentTimezone(fecha);
    return hoy.tz('America/Mexico_City').toDate();
  }
}
