import { Injectable } from '@angular/core';
import { IinvitacionesEmpresaSinContratoPaginadas } from '@app/modulos/dashboard/modelo/interfaces/IInvitacionEmpresaSinContrato.type';
import { Observable } from 'rxjs';
import { ApiService } from '@core/servicios/api.service';
import { fromPromise } from 'rxjs/internal-compatibility';

@Injectable()
export class SharedService {
  constructor(private apiService: ApiService) {}

  consultarInvitacionesEmpresa(
    nombreEmpresa: string,
    offset: number,
    limit: number,
    ordernarPorColumna: string,
    tipoOrden: string
  ): Observable<IinvitacionesEmpresaSinContratoPaginadas> {
    return fromPromise(
      this.apiService.get('empresa/sin_contrato/invitaciones', {
        nombre_empresa: nombreEmpresa,
        offset,
        limit,
        ordernar_por: ordernarPorColumna,
        tipo_orden: tipoOrden,
      })
    );
  }

  actualizarFechaExpiracionInvitacionEmpresaSinContrato(data: {
    uuidInvitacion: string;
    fechaExpiracion: string;
  }): Promise<any> {
    return this.apiService.post(
      'empresa/sin_contrato/invitaciones/actualizar_fecha_expiracion',
      data
    );
  }
}
