import { Injectable } from '@angular/core';
import Auth from '@aws-amplify/auth';
import API from '@aws-amplify/api';
import { UtilsService } from '@qiip/core';

@Injectable({
  providedIn: 'root',
})
export class ApiService {
  private API_NAME = 'qiipApiGateway';
  private headers = {
    'Content-Type': 'application/json',
  };

  constructor(private utilsService: UtilsService) {}

  getCurrentUser() {
    return Auth.currentAuthenticatedUser();
  }

  async isAuthenticated() {
    try {
      const sesion = await this.getCurrentUser();
      return !!sesion;
    } catch (e) {
      return false;
    }
  }

  getCurrentSesion() {
    return Auth.currentSession();
  }

  confirmSignUp(phone: string, code: string): Promise<any> {
    return Auth.confirmSignUp(phone, code);
  }

  signUp(body: any): Promise<any> {
    return Auth.signUp(body);
  }

  singIn(user: string, password: string): Promise<any> {
    return Auth.signIn(user, password);
  }

  singOut(): Promise<any> {
    return Auth.signOut();
  }

  get(url: string, queryParams?: any, headers?: any): Promise<any> {
    return API.get(this.API_NAME, url, {
      headers: this.getHeaders(headers),
      queryStringParameters: queryParams,
    });
  }

  getBlob(url: string, queryParams?: any, headers?: any): Promise<any> {
    return API.get(this.API_NAME, url, {
      headers: this.getHeaders(headers),
      queryStringParameters: queryParams,
      responseType: 'blob',
    });
  }

  post(url: string, body: any, headers?: any): Promise<any> {
    return API.post(this.API_NAME, url, {
      headers: this.getHeaders(headers),
      body,
    });
  }

  async refreshSesion() {
    try {
      const cognitoUser = await this.getCurrentUser();
      const currentSession = await this.getCurrentSesion();
      cognitoUser.refreshSession(
        currentSession.getRefreshToken(),
        (err, session) => {
          console.error('session', err, session);
        }
      );
      return;
    } catch (e) {
      console.error('Unable to refresh Token', e);
      return;
    }
  }

  private getCorrelationIdHeader() {
    return { 'correlation-id': this.utilsService.uuid() };
  }

  private getHeaders(headers: any): any {
    return Object.assign(
      {},
      this.headers,
      headers,
      this.getCorrelationIdHeader()
    );
  }
}
