import { RouterModule, Routes } from '@angular/router';
import { AuthRouterGuard } from '@core/guards/auth-router.guard';
import { NgModule } from '@angular/core';
import { AuthRolGuard } from '@core/guards/auth-rol.guard';

const routes: Routes = [
  { path: '', redirectTo: '/login', pathMatch: 'full' },
  {
    path: 'dashboard',
    canLoad: [AuthRouterGuard, AuthRolGuard],
    loadChildren: () =>
      import('./modulos/dashboard/dashboard.module').then(
        (m) => m.DashboardModule
      ),
    data: { rol: 'operador-back' },
  },
  {
    path: 'comercial',
    canLoad: [AuthRouterGuard, AuthRolGuard],
    loadChildren: () =>
      import('./modulos/comercial/comercial.module').then(
        (m) => m.ComercialModule
      ),
    data: { rol: 'comercial' },
  },
  {
    path: 'coaching',
    canLoad: [AuthRouterGuard, AuthRolGuard],
    loadChildren: () =>
      import('./modulos/coaching/coaching.module').then(
        (m) => m.CoachingModule
      ),
    data: { rol: 'coach' },
  },
];

@NgModule({
  imports: [RouterModule.forRoot(routes, { relativeLinkResolution: 'legacy' })],
  exports: [RouterModule],
})
export class AppRoutingModule {}
