import { LOCALE_ID, NgModule } from '@angular/core';
import { rootReducer } from './CombineReducers';
import { rootEffects } from './CombineEffects';
import lcoaleDeAt from '@angular/common/locales/es-MX';
import { EffectsModule } from '@ngrx/effects';
import { PaisAmbienteGuard } from '@core/guards/pais-ambiente.guard';
import { StoreModule } from '@ngrx/store';
import { FormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { AppRoutingModule } from '@app/app-routing.module';
import { CargandoService } from '@app/shared/servicios/cargando.service';
import { SharedModule } from '@app/shared/shared.module';
import { AuthRouterGuard } from '@core/guards/auth-router.guard';
import { AppComponent } from '@app/app.component';
import {
  HashLocationStrategy,
  LocationStrategy,
  registerLocaleData,
} from '@angular/common';
import { UsuarioModule } from '@usuario/usuario.module';
import { QiipLibModule } from '@qiip/lib';

registerLocaleData(lcoaleDeAt);

@NgModule({
  declarations: [AppComponent],
  imports: [
    BrowserAnimationsModule,
    FormsModule,
    StoreModule.forRoot(rootReducer),
    EffectsModule.forRoot(rootEffects),
    AppRoutingModule,
    UsuarioModule,
    SharedModule,
    QiipLibModule,
  ],
  providers: [
    { provide: LocationStrategy, useClass: HashLocationStrategy },
    { provide: LOCALE_ID, useValue: 'es-mx' },
    AuthRouterGuard,
    CargandoService,
    PaisAmbienteGuard,
  ],
  bootstrap: [AppComponent],
})
export class AppModule {}
