import {
  ACTUALIZAR_ZONA_HORARIA,
  CONSULTA_COACHES_SUCCESS,
  CONSULTA_SESIONES_COACH_EXPIRED,
  CONSULTA_SESIONES_COACH_SUCCESS,
  CONSULTA_TEMAS_SUCCESS,
} from '../actions/coaching.action';
import { environment } from '../../../../../environments/environment';
import { Sesion } from '../../modelo/sesion.model';
import * as momentTimezone from 'moment-timezone';

const initialState = {
  idCoach: null,
  nombreCompleto: null,
  idSesion: null,
  rol: null,
  sesionesVigentes: [],
  sesionesVencidas: [],
  sesionesAgendadas: [],
  temas: [],
  coaches: [],
  zonaHoraria: environment.zone,
};

export function coachingReducer(state = initialState, { type, payload }) {
  switch (type) {
    case CONSULTA_SESIONES_COACH_SUCCESS:
      return Object.assign({}, state, {
        idCoach: payload.idCoach,
        nombreCompleto: payload.nombreCompleto,
        idSesion: payload.idSesion,
        rol: payload.rol,
        sesionesVigentes: actualizarFechasSegunZona(
          payload.sesionesVigentes,
          state.zonaHoraria
        ),
      });
    case CONSULTA_SESIONES_COACH_EXPIRED:
      return Object.assign({}, state, {
        sesionesVencidas: actualizarFechasSegunZona(
          payload.sesionesVigentes,
          state.zonaHoraria
        ),
      });
    case CONSULTA_TEMAS_SUCCESS:
      return Object.assign({}, state, {
        temas: payload,
      });
    case CONSULTA_COACHES_SUCCESS:
      return Object.assign({}, state, {
        coaches: payload,
      });
    case ACTUALIZAR_ZONA_HORARIA:
      return Object.assign({}, state, {
        zonaHoraria: payload,
        sesionesVigentes: actualizarFechasSegunZona(
          state.sesionesVigentes,
          payload
        ),
        sesionesVencidas: actualizarFechasSegunZona(
          state.sesionesVencidas,
          payload
        ),
      });
    default:
      return state;
  }
}

function actualizarFechasSegunZona(
  sesionesVigentes: Sesion[],
  zonaHoraria: string
) {
  return sesionesVigentes.map((value) => ({
    ...value,
    fechaInicioMoment: momentTimezone(value.fechaInicio).tz(zonaHoraria),
    fechaFinMoment: momentTimezone(value.fechaFin).tz(zonaHoraria),
  }));
}
