import { Component, NgZone, OnInit } from '@angular/core';
import { Router } from '@angular/router';

import { Store } from '@ngrx/store';
import { AUTENTICAR_USUARIO_SUCCESS } from '../../state-management/actions/usuario.action';
import { UserIdleService } from '@app/shared/idle-user/idle-usuario.service';
import { environment } from '@env/environment';
import { ApiService } from '@core/servicios/api.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.scss'],
})
export class LoginComponent implements OnInit {
  mostrarLoginError = false;
  cargando: boolean;

  constructor(
    private ngZone: NgZone,
    private router: Router,
    private store: Store<any>,
    private apiService: ApiService,
    private userIdle: UserIdleService
  ) {}

  ngOnInit() {}

  autenticar(usuario: string, password: string) {
    this.cargando = true;
    this.apiService
      .singIn(usuario, password)
      .then((cognitoUserSession) => {
        return {
          sub: cognitoUserSession.attributes.sub,
          username: cognitoUserSession.username,
          email: cognitoUserSession.attributes.email,
          rol: cognitoUserSession.attributes['custom:role'],
          uuid: cognitoUserSession.attributes['custom:uuid'],
          pais: cognitoUserSession.attributes['custom:pais'],
        };
      })
      .then(
        (userData) => {
          this.cargando = false;
          this.store.dispatch({
            type: AUTENTICAR_USUARIO_SUCCESS,
            payload: userData,
          });
          this.mostrarLoginError = false;
          this.userIdle.startWatching();
          this.redireccionar(userData.rol, userData.pais);
        },
        (err) => {
          console.error(err);
          this.mostrarLoginError = true;
          this.cargando = false;
        }
      );
  }

  redireccionar(rol: string, pais: string) {
    this.ngZone.run(() => {
      if (this.paisUsuarioHabilitadoParaPaisAmbiente(pais)) {
        if (rol === 'coach') {
          this.router.navigateByUrl('coaching');
        } else if (rol === 'comercial') {
          this.router.navigateByUrl('comercial');
        } else {
          this.router.navigateByUrl('dashboard');
        }
      } else {
        this.apiService.singOut();
        this.mostrarLoginError = true;
      }
    });
  }

  paisUsuarioHabilitadoParaPaisAmbiente(pais: string) {
    return (
      !pais ||
      pais
        .split('-')
        .some(
          (value) =>
            value.toUpperCase() === environment.envCountry.toUpperCase()
        )
    );
  }
}
