import { Component, OnInit } from '@angular/core';
import { ApiService } from '@core/servicios/api.service';

@Component({
  selector: 'btn-cerrar-session',
  templateUrl: './btn-cerrar-session.component.html',
  styleUrls: ['./btn-cerrar-session.component.scss'],
})
export class BtnCerrarSessionComponent implements OnInit {
  constructor(private apiService: ApiService) {}

  ngOnInit() {}

  cerrarSesion() {
    this.apiService.singOut();
    setTimeout(() => {
      window.location.reload();
    }, 100);
  }
}
