import { Injectable } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivate,
  CanLoad,
  Route,
  Router,
  RouterStateSnapshot,
  UrlSegment,
} from '@angular/router';
import { ApiService } from '@core/servicios/api.service';

@Injectable({
  providedIn: 'root',
})
export class AuthRouterGuard implements CanActivate, CanLoad {
  constructor(public router: Router, private apiService: ApiService) {}

  canActivate(
    route: ActivatedRouteSnapshot,
    state: RouterStateSnapshot
  ): Promise<boolean> {
    return this.validarAccesoSegunSesion();
  }

  canLoad(route: Route, segments: UrlSegment[]): Promise<boolean> {
    return this.validarAccesoSegunSesion();
  }

  private async validarAccesoSegunSesion() {
    try {
      const sesion = await this.apiService.getCurrentUser();
      if (sesion) {
        return true;
      } else {
        return this.denegarAcceso();
      }
    } catch (e) {
      return this.denegarAcceso();
    }
  }

  private denegarAcceso() {
    this.router.navigate(['login']);
    return false;
  }
}
