import {
  AUTENTICAR_USUARIO_SUCCESS,
  CERRAR_SESSION_SUCCESS,
} from '../actions/usuario.action';
import { combineReducers } from '@ngrx/store';

const initialState = {
  sub: null,
  username: null,
  email: null,
  rol: null,
  uuid: null,
  pais: null,
};
const appReducer = combineReducers({});

export function usuarioReducer(state = initialState, { type, payload }) {
  switch (type) {
    case AUTENTICAR_USUARIO_SUCCESS:
      return Object.assign({}, state, {
        sub: payload.sub,
        username: payload.username,
        email: payload.email,
        rol: payload.rol,
        uuid: payload.uuid,
        pais: payload.pais,
      });
    case CERRAR_SESSION_SUCCESS:
      return appReducer(undefined, type);

    default:
      return state;
  }
}
